import React, { useState, useEffect, useRef } from "react";
import { Row, Col, Button, Modal, Form } from "react-bootstrap";
import toast from "react-hot-toast";

import api from "../../../../services/api";

import useAuth from "../../../../hooks/useAuth";

import JoditEditor from "jodit-react";
import { geradorTextoDespacho } from "../geradorTexto";

const TermoEncerramento = ({
  processo_ID,
  setprocesso_ID,

  uploadlist,
  pessoalist,
  decisaolist,
  isOpenEncerrar,
  setIsOpenEncerrar,
  instrucao_ID,
}) => {

  const { popUp_alertaOK } = useAuth();

  let parecerEdit = "";

  const editorREF = useRef(null);

  const [DATA_D, setDATA_D] = useState("");
  
  const setRef = (jodit) => {
    // control
  };

  //-------------- Despacho -------------------------

  const [editorcontent, seteditorcontent] = useState("");

  const updateContent = (value) => {
    parecerEdit = value;

    console.log(parecerEdit);
  };


  useEffect(() => {
     setDATA_D("")
    }, []);

  const geraText = async () => {
    try {
      const response = await api.get(`/termo-encerramento/texto`);
      if (response.status == "200" && response.data.OBS) {
        seteditorcontent(response.data.OBS);
        parecerEdit = response.data.OBS;
      }
    } catch (error) {}
  };

  async function submeterTermoEncerramento(event) {
    event.preventDefault();
    let valido = true
     if (DATA_D == "" || editorREF?.current?.value == "") {
       valido = false
        popUp_alertaOK("Preencha todos os campos obrigatrios");
      } 
    if (valido) {
      const upload = {
        DATA: DATA_D,
        DESPACHO: editorREF?.current?.value,
        PROCESSO_EXCLUSAO_ID: processo_ID
      };

      try {
        const response = await api.put(
          `/sgigjprocessoexclusao/${instrucao_ID}/termo-encerramento`,
          upload
        );

        if (response.status == "200") {
          toast.success("Termo de Encerramento enviado!", { duration: 4000 });
          uploadlist();
          setIsOpenEncerrar(false);
        }else {
          toast.error("Erro ao enviar Termo de Encerramento", { duration: 4000 });
        }
      } catch (err) {
        console.error(err.response);
        toast.error("Erro ao enviar Termo de Encerramento", { duration: 4000 });
      }
    }
  }

  return (
    <Modal size="xl" show={isOpenEncerrar} onHide={() => setIsOpenEncerrar(false)}>
      <Modal.Header closeButton>
        <Modal.Title as="h5">Termo de Encerramento</Modal.Title>
      </Modal.Header>
      <Modal.Body className="newuserbox">
        <form
          id="submeterTermoEncerramento"
          onSubmit={submeterTermoEncerramento}
        >
          <Row>
            <Col sm={3}>
              <div className="form-group fill">
                <label className="floating-label" htmlFor="Name">
                  Data <span style={{ color: "red" }}>*</span>
                </label>
                <input
                  type="date"
                  onChange={(event) => {
                    setDATA_D(event.target.value);
                  }}
                  defaultValue={DATA_D}
                  className="form-control"
                  placeholder="Data..."
                  required
                />
              </div>
            </Col>

            <Col sm={12}>
              <div className="form-group fill">
                <label className="floating-label" htmlFor="Name">
                  Despacho
                </label>

                <JoditEditor
                  editorRef={setRef}
                  value={editorcontent}
                  config={{
                    readonly: false,
                  }}
                  ref={editorREF}
                  onChange={(event) => updateContent(event)}
                />
              </div>
            </Col>
          </Row>
        </form>
      </Modal.Body>

      <Modal.Footer>
        <Button variant="danger" onClick={() => setIsOpenEncerrar(false)}>
          Fechar
        </Button>
        <Button onClick={() => geraText()} variant="primary">
          Gerar texto
        </Button>
        <Button
          onClick={() => {}}
          type="submit"
          form="submeterTermoEncerramento"
          variant="primary"
        >
          Concluir
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
export default TermoEncerramento;
